import { defineComponent, defineAsyncComponent } from 'vue';
import { mapGetters, mapMutations } from 'vuex';
import Search from '@/components/common/Search/Search.vue';
const SearchTip = () => defineAsyncComponent(() => import('@/components/common/SearchTip/SearchTip.vue'));
const FavoriteCities = () => defineAsyncComponent(() => import('@/components/common/FavoriteCities/FavoriteCities.vue'));
export default defineComponent({
    name: 'Home',
    components: {
        Search,
        FavoriteCities: FavoriteCities(),
        SearchTip: SearchTip(),
    },
    computed: {
        ...mapGetters(['getTestString']),
    },
    data() {
        return {
            temp: '0',
        };
    },
    methods: {
        ...mapGetters({
            getFavoriteCities: 'getFavoriteCities',
        }),
        ...mapMutations({
            setFavoriteCities: 'setFavoriteCities',
            toggleVisibilityHeader: 'toggleVisibilityHeader',
        }),
    },
    beforeMount() {
        if (localStorage.getItem('favoriteCities')?.split(',')[0] === '') {
            this.setFavoriteCities(localStorage.removeItem('favoriteCities'));
        }
        else {
            this.setFavoriteCities(localStorage.getItem('favoriteCities')?.split(','));
        }
    },
    mounted() {
        this.toggleVisibilityHeader(true);
        window.addEventListener('resize', () => {
            this.toggleVisibilityHeader(true);
        });
    }
});
