export const favoriteCitiesStoreModule = {
    state: () => ({
        cities: []
    }),
    getters: {
        getFavoriteCities(state) {
            return state.cities;
        },
    },
    mutations: {
        setFavoriteCities(state, cities) {
            if (cities) {
                state.cities = cities;
            }
        },
        addCity(state, cityName) {
            state.cities.push(cityName);
            localStorage.setItem('favoriteCities', String([state.cities]));
        },
        removeCity(state, cityName) {
            state.cities = state.cities.filter((item) => item !== cityName);
            localStorage.setItem('favoriteCities', String([state.cities]));
        }
    },
    actions: {},
};
