import axios from 'axios';
const axiosInit = () => {
    const defaultConfig = {
        headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json; charset=utf-8',
        },
    };
    const axiosInstanceWithConfig = axios.create(defaultConfig);
    axiosInstanceWithConfig.interceptors.request.use((config) => {
        return {
            ...config,
        };
    }, (error) => {
        return Promise.reject(error);
    });
    axiosInstanceWithConfig.interceptors.response.use((response) => response, async (error) => {
        return Promise.reject(error);
    });
    return axiosInstanceWithConfig;
};
export const axiosInstance = axiosInit();
export default {
    get: axiosInstance.get,
    post: axiosInstance.post,
    put: axiosInstance.put,
    delete: axiosInstance.delete,
};
