import { axiosInstance } from '@/services/httpService';
export const API_ENDPOINT = {
    getCurrentWeather(cityName) {
        return (axiosInstance.get(`https://api.openweathermap.org/data/2.5/weather?q=${cityName}&appid=83d9c12644f83eb94f3ad2d262c259e2&units=metric&lang=ru`));
    },
    getSearchList(searchQuery) {
        return (axiosInstance.post('https://suggestions.dadata.ru/suggestions/api/4_1/rs/suggest/address', {
            query: searchQuery,
            count: 5,
            from_bound: {
                value: 'city',
            },
            to_bound: {
                value: 'city',
            },
        }, {
            headers: {
                'Content-Type': 'application/json',
                Accept: 'application/json',
                Authorization: 'Token ff1147997432ca2cf52c3f7fa54ab166d98ff013',
            },
        }));
    },
    signIn: 'Api/test',
};
