import { API_ENDPOINT } from '@/constants/api';
export const currentWeatherStoreModule = {
    state: () => ({
        headerIsVisible: true,
        isFound: true,
        cod: 400,
        cityName: '',
        temp: 0,
        description: '',
        pressure: null,
        sunset: null
    }),
    mutations: {
        toggleIsFound(state, type) {
            state.isFound = type;
        },
        setCityName(state, cityName) {
            state.cityName = cityName;
        },
        setWeather(state, weatherInfo) {
            state.cod = weatherInfo.weather[0].id;
            state.temp = weatherInfo.main.temp;
            state.pressure = weatherInfo.main.pressure;
            state.description = weatherInfo.weather[0].description;
            state.cityName = weatherInfo.name;
            state.sunset = weatherInfo.sys.sunset;
        },
        toggleVisibilityHeader(state, type) {
            state.headerIsVisible = type;
        }
    },
    actions: {
        async getCurrentWeather(state) {
            try {
                const response = await API_ENDPOINT.getCurrentWeather(state.state.cityName);
                this.commit('toggleIsFound', true);
                this.commit('setWeather', response.data);
            }
            catch (e) {
                const res = e;
                if (res.message.split(' ').reverse()[0]) {
                    this.commit('toggleIsFound', false);
                }
                else {
                    this.commit('toggleIsFound', true);
                }
            }
        }
    },
};
