import { defineComponent } from 'vue';
import { mapActions, mapMutations, mapState } from 'vuex';
import PressureSvg from '@/assets/svg/PressureSvg.vue';
import ThunderstormSvg from '@/assets/svg/ThunderstormSvg.vue';
import DizzleSvg from '@/assets/svg/DizzleSvg.vue';
import RainSvg from '@/assets/svg/RainSvg.vue';
import FogSvg from '@/assets/svg/FogSvg.vue';
import DustSvg from '@/assets/svg/DustSvg.vue';
import SqualSvg from '@/assets/svg/SqualSvg.vue';
import TornadoSvg from '@/assets/svg/TornadoSvg.vue';
import ClearSvg from '@/assets/svg/ClearSvg.vue';
import CloudsSvg from '@/assets/svg/CloudsSvg.vue';
import SnowSvg from '@/assets/svg/SnowSvg.vue';
export default defineComponent({
    name: 'CurrentWeather',
    components: {
        PressureSvg,
        ThunderstormSvg,
        DizzleSvg,
        RainSvg,
        FogSvg,
        DustSvg,
        SqualSvg,
        TornadoSvg,
        ClearSvg,
        CloudsSvg,
        SnowSvg
    },
    computed: {
        ...mapState({
            weatherInfo: (state) => {
                const store = state;
                return store.currentWeatherStoreModule;
            },
            isVisible: (state) => {
                const store = state;
                return store.currentWeatherStoreModule.headerIsVisible;
            }
        }),
    },
    data() {
        return {
            sunsetTime: '',
            weatherDescription: ''
        };
    },
    methods: {
        ...mapActions({
            getCurrentWeather: 'getCurrentWeather'
        }),
        ...mapMutations({
            setCityName: 'setCityName',
            toggleVisibilityHeader: 'toggleVisibilityHeader'
        })
    },
    async mounted() {
        if (window.innerWidth <= 768) {
            this.toggleVisibilityHeader(false);
        }
        else {
            this.toggleVisibilityHeader(true);
        }
        window.addEventListener('resize', () => {
            if (window.innerWidth <= 768) {
                this.toggleVisibilityHeader(false);
            }
            else {
                this.toggleVisibilityHeader(true);
            }
        });
        await this.setCityName(this.$route.params.city);
        await this.getCurrentWeather();
        this.weatherDescription = String([this.weatherInfo.description[0].toUpperCase(), this.weatherInfo.description.slice(1)].join(''));
        this.sunsetTime = String(new Date(this.weatherInfo.sunset * 1000).toLocaleTimeString());
    },
});
