import ArrowBackSvg from '@/assets/svg/ArrowBackSvg.vue';
import FavotitesSvg from '@/assets/svg/FavotitesSvg.vue';
import { defineComponent } from 'vue';
import { mapGetters, mapMutations, mapState } from 'vuex';
import FavoriteFillSvg from '@/assets/svg/FavoriteFillSvg.vue';
import router from '@/router';
export default defineComponent({
    components: { ArrowBackSvg, FavotitesSvg, FavoriteFillSvg },
    name: 'CurrentWeatherNav',
    mounted() {
        this.setFavoriteCities(localStorage.getItem('favoriteCities')?.split(','));
        this.isFavoriteCheck();
    },
    data() {
        return {
            isFavorite: false
        };
    },
    computed: {
        ...mapState({
            isFound: (state) => {
                const store = state;
                return store.currentWeatherStoreModule.isFound;
            }
        })
    },
    methods: {
        ...mapGetters({
            getFavoriteCities: 'getFavoriteCities'
        }),
        ...mapMutations({
            addCity: 'addCity',
            removeCity: 'removeCity',
            setFavoriteCities: 'setFavoriteCities',
            toggleVisibilityHeader: 'toggleVisibilityHeader'
        }),
        toHome() {
            router.push('/');
            this.toggleVisibilityHeader(true);
        },
        setIsFavorite() {
            this.isFavorite = this.getFavoriteCities()
                .some((cityName) => cityName === this.$route.params.city);
        },
        isFavoriteCheck() {
            this.isFavorite = this.getFavoriteCities()
                .some((item) => item === this.$route.params.city);
        },
        removeFavorite() {
            this.removeCity(this.$route.params.city);
            this.isFavoriteCheck();
        },
        addFavorite() {
            this.addCity(this.$route.params.city);
            this.isFavoriteCheck();
        },
    },
});
