import { defineComponent } from 'vue';
import { mapActions, mapMutations, mapState } from 'vuex';
import SearchComponent from '@/components/ui/SearchComponent/SearchComponent.vue';
import { API_ENDPOINT } from '@/constants/api';
export default defineComponent({
    name: 'Search',
    components: {
        SearchComponent,
    },
    data() {
        return {
            autocompleteActive: false,
            items: ['']
        };
    },
    methods: {
        ...mapActions({
            getWeather: 'getCurrentWeather',
        }),
        ...mapMutations({
            addCity: 'addCity',
            removeCity: 'removeCity',
            setSearchQuery: 'setSearchQuery',
            toggleVisibilityHeader: 'toggleVisibilityHeader'
        }),
        async autocompleteCheck() {
            this.items = [];
            if (this.searchQuery.length >= 3) {
                this.autocompleteActive = true;
                const response = await API_ENDPOINT.getSearchList(this.searchQuery);
                const searchCities = response.data.suggestions.map((item) => item.data.city);
                this.items = searchCities.filter((item) => !item.split(' ').includes(''));
            }
            else {
                this.autocompleteActive = false;
            }
        },
        toWeatherPage(item) {
            this.$router.push(`/weather=${item}`);
            this.setSearchQuery('');
            this.toggleVisibilityHeader(false);
        }
    },
    computed: {
        ...mapState({
            searchQuery: (state) => {
                const store = state;
                return store.searchStoreModule.searchQuery;
            },
        }),
    }
});
